.navigation-btn {
    position: absolute;
    width: 28px;
    height: 24px;
    border-radius: 8px;
    box-shadow: 0px 2px 6px 0px #0000001F;
    background-color: var(--white);
    z-index: 1;
    bottom: 0;
    transition: opacity .3s ease-in-out;
    &::before {
        content: '';
        border-bottom: 1px solid var(--text-color);
        border-right: 1px solid var(--text-color);
        width: 5px;
        height: 5px;
        position: absolute;
        top: calc(50% - 2.5px);
    }
    &:disabled {
        opacity: 0;
        visibility: hidden;
    }
}

.btn-next {
  right: 0;
  &::before {
      left: calc(50% - 4px);
      transform: rotate(-45deg);
    }
}

.btn-prev {
    left: 0;
    &::before {
        left: calc(50% - 1px);
        transform: rotate(135deg);
    }
}


